export const gen12StringToUTF = (
  bytes: Uint8Array,
  offset: number,
  length: number
) => {
  let str = '';
  // console.log(bytes);
  for (let i = offset; i < offset + length; i += 1) {
    // console.log(i, bytes[i], GBStringDict[bytes[i]]);
    if (bytes[i] === G1_TERMINATOR) {
      break;
    }
    str += GBStringDict[bytes[i]] ?? '';
  }
  return str;
};

export const utf16StringToGen12 = (
  str: string,
  length: number,
  terminate: boolean
) => {
  var bufView = new Uint8Array(length);
  for (var i = 0; i < Math.min(str.length, length); i++) {
    let gen12DictEntry = Object.entries(GBStringDict).find(
      ([key, val]) => val === str.charAt(i)
    );
    if (str.charCodeAt(i) === 0) {
      break;
    } else if (!gen12DictEntry) {
      bufView[i] = 0xe6;
    } else {
      bufView[i] = parseInt(gen12DictEntry[0]);
    }
  }
  if (terminate) {
    let terminalIndex = Math.min(str.length, length - 1);
    bufView[terminalIndex] = G1_TERMINATOR;
  }
  return bufView;
};

export const gen3StringToUTF = (
  bytes: Uint8Array,
  offset: number,
  length: number
) => {
  let str = '';
  for (let i = offset; i < offset + length; i += 1) {
    if (bytes[i] === 0xff) {
      return str;
    }
    str += Gen3CharacterSet[bytes[i]];
  }
  return str;
};

export const utf16StringToGen3 = (
  str: string,
  length: number,
  terminate: boolean,
  terminateFill: boolean
) => {
  var bufView = new Uint8Array(length);
  for (var i = 0; i < Math.min(str.length, length); i++) {
    let gen3Char = Gen3CharacterSet.indexOf(str.charAt(i));
    if (str.charCodeAt(i) === 0) {
      break;
    } else if (gen3Char === -1) {
      // missing characters are now '?'
      bufView[i] = 172;
    } else {
      bufView[i] = gen3Char;
    }
  }
  if (terminateFill) {
    const terminalIndex = Math.min(length - 1, str.length);
    bufView.set(
      new Uint8Array(length - terminalIndex).fill(0xff),
      terminalIndex
    );
  } else if (terminate) {
    const terminalIndex = Math.min(length - 1, str.length);
    bufView[terminalIndex] = 0xff;
  }
  return bufView;
};

export const gen4StringToUTF = (
  bytes: Uint8Array,
  offset: number,
  length: number
) => {
  let str = '';
  for (let i = 0; i < length; i += 1) {
    let value = bytesToUint16LittleEndian(bytes, offset + 2 * i);
    if (value === 0xffff) {
      return str;
    }
    str += String.fromCharCode(Gen4ToUTFMap[value]);
  }
  return str;
};

export const utf16StringToGen4 = (
  str: string,
  length: number,
  terminate: boolean
) => {
  var buf = new ArrayBuffer(length * 2);
  var bufView = new Uint16Array(buf);
  for (var i = 0; i < length; i++) {
    console.log(str, str.length, str.charCodeAt(i));
    if (i >= str.length || str.charCodeAt(i) === 0) {
      console.log('adding terminator');
      if (terminate) {
        bufView[i] = 0xffff;
      }
      break;
    }
    let gen4Char = UTFToGen4Map[str.charCodeAt(i)];
    if (gen4Char === -1) {
      // missing characters are now '?'
      bufView[i] = 0x01ac;
    } else {
      bufView[i] = gen4Char;
    }
  }

  return new Uint8Array(buf);
};

export const gen5StringToUTF = (
  bytes: Uint8Array,
  offset: number,
  length: number
) => {
  let str = '';
  for (let i = 0; i < length; i += 1) {
    let value = bytesToUint16LittleEndian(bytes, offset + 2 * i);
    if (value === 0xffff) {
      return str;
    }
    str += String.fromCharCode(value);
  }
  return str;
};

export const utf16StringToGen5 = (
  str: string,
  length: number,
  terminate: boolean
) => {
  var buf = new ArrayBuffer(length * 2);
  var bufView = new Uint16Array(buf);
  for (var i = 0; i < length; i++) {
    console.log(str, str.length, str.charCodeAt(i));
    if (i >= str.length || str.charCodeAt(i) === 0) {
      console.log('adding terminator');
      if (terminate) {
        bufView[i] = 0xffff;
      }
      break;
    }
    bufView[i] = str.charCodeAt(i);
  }

  return new Uint8Array(buf);
};

export const utf16BytesToString = (
  bytes: Uint8Array,
  offset: number,
  length: number
) => {
  let byteArray = new Uint16Array(length);
  for (let i = 0; i < length; i += 1) {
    let value = bytesToUint16LittleEndian(bytes, offset + 2 * i);
    if (value === 0) {
      break;
    }
    byteArray[i] = value;
  }
  let stringLength = byteArray.indexOf(0);
  if (stringLength < 0) {
    stringLength = length;
  }
  return new TextDecoder('utf-16').decode(byteArray.slice(0, stringLength));
};

export const utf16StringToBytes = (str: string, length: number) => {
  var buf = new ArrayBuffer(length * 2);
  var bufView = new Uint16Array(buf);
  for (var i = 0; i < Math.min(str.length, length); i++) {
    bufView[i] = str.charCodeAt(i);
  }
  return new Uint8Array(buf);
};

